import React, { Component } from 'react';
import { NavBar } from 'antd-mobile';
import queryString from 'query-string';

import LockDesc from './LockDesc';
import LockId from './LockId';
import LockMobile from './LockMobile';
import LockResult from './LockResult';
import { webviewDestroy } from '../../../util';
import { getNavBarTitle } from '../common';

import '../css/PageCss.less';

class Lock extends Component {
  state = {
    steps: 'LockDesc',
    status: '',
    resultDesc: '',
    appInfo: null,
  };

  startLock = () => {
    this.setState({
      steps: 'LockId',
    });
  };

  lockId = () => {
    this.setState({
      steps: 'LockMobile',
    });
  };

  lockMobile = (status, resultDesc) => {
    this.setState({
      steps: 'LockResult',
      status,
      resultDesc,
    });
  };

  endLock = () => {
    webviewDestroy();
  };

  onPressBack = () => {
    const { steps } = this.state;
    if (steps === 'LockDesc') {
      /* eslint-disable */
      window.location.href = location.href.replace('lock', 'center');
      /* eslint-enable */
    } else if (steps === 'LockId') {
      this.setState({
        steps: 'LockDesc',
      });
    } else if (steps === 'LockMobile') {
      this.setState({
        steps: 'LockId',
      });
    } else if (steps === 'LockResult') {
      webviewDestroy();
    }
  };

  getAppInfo = () => {
    /* eslint-disable */
    const values = queryString.parse(location.search);
    /* eslint-enable */
    const did = values.did;
    if (did) {
      this.setState({
        appInfo: { device_id: did },
      });
      return;
    }
    window.ToutiaoJSBridge.call('appInfo', {}, (appInfo) => {
      this.setState({
        appInfo,
      });
    });
  };

  componentDidMount() {
    this.getAppInfo();
  }

  render() {
    const { steps, status, resultDesc, appInfo } = this.state;
    const navBarTitle = getNavBarTitle(steps, 'LOCK');
    return (
      <div className="lock-content">
        <NavBar
          className={
            steps === 'LockDesc' ? 'i-navbar' : 'i-navbar i-navbar-border'
          }
          mode="dark"
          leftContent={<div className="icon-back" />}
          onLeftClick={() => {
            this.onPressBack();
          }}
        >
          {navBarTitle}
        </NavBar>
        {steps === 'LockDesc' && <LockDesc onClick={this.startLock} />}
        {steps === 'LockId' && (
          <LockId onClick={this.lockId} appInfo={appInfo} />
        )}
        {steps === 'LockMobile' && (
          <LockMobile onClick={this.lockMobile} appInfo={appInfo} />
        )}
        {steps === 'LockResult' && (
          <LockResult
            onClick={this.endLock}
            status={status}
            description={resultDesc}
          />
        )}
      </div>
    );
  }
}

export default Lock;



