import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';

let sliderObj = null;

class Slider extends PureComponent {
  initSliderView = () => {
    const { onSuccess, onClose, options } = this.props;
    const opts = {
      ele: 'h5_slide', // id
      host: 'https://verify.snssdk.com', //  
      // host: 'http://10.8.121.119:6789', // 
      aid: '1128', // appid   1128 
      lang: 'zh',
      app_name: 'aweme',
      challenge_code: '1105',
      toolbarBackColor: '#fff', // 
      promptBackColor: '#F0F0F0', // 
      promptFontColor: '#808080', // 
      refreshFontColor: '#FE2C55', // 
      refreshIconColor: '#FE2C55', // 
      validatePassBackColor: '#A0CC49', // 
      validateFailBackColor: '#EB2F2F', // 
      successCb() {
        onSuccess();
      }, // 
      errorCb() {
        onClose();
      }, // 
      ...options,
    };
    /* eslint-disable */
    if (SliderVerification) {
      sliderObj = new SliderVerification(opts);
      /* eslint-enable */
      sliderObj.init();
    } else {
      console.error('SliderVerification error');
    }
  };

  componentDidMount() {
    this.initSliderView();
  }

  render() {
    return (
      <div style={{ backgroundColor: '#fff' }}>
        <div id="h5_slide" />
      </div>
    );
  }
}

export default Slider;

export const SliderFunc = {
  show() {
    sliderObj.show();
  },
  hide() {
    sliderObj.hide();
  },
  init() {
    sliderObj.init();
  },
};

Slider.propTypes = {
  onSuccess: PropTypes.func,
  onClose: PropTypes.func,
  options: PropTypes.object,
};

Slider.defaultProps = {
  onSuccess: () => {},
  onClose: () => {},
  options: {},
};



