export const getNavBarTitle = (steps, type) => {
  let navBarTitle = '';
  switch (steps) {
    case 'LockId':
      navBarTitle =
        type === 'LOCK'
          ? ''
          : '';
      break;
    case 'LockMobile':
      navBarTitle = '';
      break;
    default:
      break;
  }
  return navBarTitle;
};

export default {};



