import React, { Component } from 'react';
import { Modal, InputItem, Flex, Button, List } from 'antd-mobile';
import { createForm } from 'rc-form';
import PropTypes from 'prop-types';

import { iToast } from '../../../util';
import Slider, { SliderFunc } from '../../../components/Slider';
import { ERROR_MSG, CONST_CODE } from '../../../constant';
import { lockQuery, sliderRegister } from '../../../action';

class LockId extends Component {
  state = {
    sliderRegisterError: false,
    modal: false,
    modalContent: '',
    showModal: false,
    userId: '',
    code: '',
  };
  bottonRef = null;

  onClickCheck = (e) => {
    e.preventDefault();
    /* eslint-disable */
    this.props.form.validateFields((err, values) => {
      /* eslint-enable */
      if (!values.userId) {
        iToast.show(ERROR_MSG.DOUYIN_USERID_EMPTY);
        return;
      }
      if (!err) {
        this.setState({
          showModal: true,
          userId: values.userId,
          code: values.code,
        });
        SliderFunc.show();
      }
    });
  };

  onClose = () => {
    this.setState({
      modal: false,
      showModal: false,
    });
    if (this.state.sliderRegisterError) {
      /* eslint-disable */
      window.location.href = location.href.replace('lock', 'center');
      /* eslint-enable */
    }
  };

  onSliderSuccess = () => {
    const { userId, code } = this.state;
    const { appInfo } = this.props;
    lockQuery({
      app_user_id: userId,
      code: code ? code.toLocaleUpperCase() : '',
      app_id: CONST_CODE.DOUYIN_AID,
      device_id: appInfo.device_id || '',
    })
      .then((res) => {
        if (res) {
          this.props.onClick();
          this.setState({
            showModal: false,
          });
        }
      })
      .catch((res) => {
        SliderFunc.hide();
        SliderFunc.init();
        this.setState({
          showModal: false,
          modal: !!res.description,
          modalContent: res.description,
        });
      });
  };

  onSliderClose = () => {
    this.setState({
      showModal: false,
    });
  };

  componentDidMount() {
    const self = this;
    const { appInfo } = this.props;
    setTimeout(() => {
      sliderRegister({
        aid: CONST_CODE.DOUYIN_AID,
        device_id: appInfo.device_id || '',
      })
        .then(() => {
          SliderFunc.show();
        })
        .catch((res) => {
          self.setState({
            sliderRegisterError: true,
            modal: !!res.description,
            modalContent: res.description,
            showModal: false,
          });
        });
    }, 500);
  }

  render() {
    /* eslint-disable */
    const { form } = this.props;
    /* eslint-enable */
    const { getFieldProps } = form;
    const { modal, modalContent } = this.state;
    return (
      <div className="all top-gap">
        <div className="i-body">
          <div className="body-content">
            <List className="i-input">
              <InputItem
                className="i-input-item"
                {...getFieldProps('userId')}
                clear
                placeholder=""
                maxLength={32}
              >
                
              </InputItem>
              <InputItem
                className="i-input-item"
                {...getFieldProps('code')}
                clear
                placeholder=""
                maxLength={4}
              >
                
              </InputItem>
            </List>
            <div className="id-desc">
              
            </div>
            <div className="my-check">
              <Flex>
                <Flex.Item>
                  <Button
                    type="warning"
                    className="check-button"
                    onClick={this.onClickCheck}
                  >
                    
                  </Button>
                </Flex.Item>
              </Flex>
            </div>
          </div>
        </div>
        <div
          className={this.state.showModal ? 'id-slider' : 'id-slider-hidden'}
        >
          <Slider
            onSuccess={this.onSliderSuccess}
            onClose={this.onSliderClose}
          />
        </div>
        <div>
          <Modal
            visible={modal}
            transparent
            maskClosable={false}
            onClose={this.onClose}
            footer={[
              {
                text: '',
                onPress: () => {
                  this.onClose();
                },
              },
            ]}
            wrapProps={{ onTouchStart: this.onWrapTouchStart }}
          >
            <div className="content-notitle">{modalContent}</div>
          </Modal>
        </div>
      </div>
    );
  }
}

const LockIdForm = createForm()(LockId);

export default LockIdForm;

LockId.propTypes = {
  onClick: PropTypes.func,
  appInfo: PropTypes.object,
};

LockId.defaultProps = {
  onClick: () => {},
  appInfo: {},
};



