import React, { Component } from 'react';
import { Button, Flex } from 'antd-mobile';
import PropTypes from 'prop-types';

class LockDesc extends Component {
  state = {
    success: true,
  };

  resultInfo = {
    success: {
      title: '',
    },
    fail: {
      title: '',
    },
  };

  onClick = () => {
    this.props.onClick();
  };

  render() {
    const { status, description } = this.props;
    const icon = status === 'success' ? 'iconfont-success' : 'iconfont-fail';
    let title = '';
    if (this.resultInfo[status]) {
      title = this.resultInfo[status].title;
    }
    return (
      <div className="all i-body body-center">
        <div className="body-content">
          <div className="i-title">
            <i className={`iconfont iconfont-xl ${icon}`} />
            <div className="title-result">{title}</div>
            <div className="sub-title">
              <div dangerouslySetInnerHTML={{ __html: description }} />
            </div>
            <div className="res-check">
              <Flex>
                <Flex.Item>
                  <Button
                    type="warning"
                    className="check-button"
                    onClick={this.onClick}
                  >
                    {'  '}
                  </Button>
                </Flex.Item>
              </Flex>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default LockDesc;

LockDesc.propTypes = {
  onClick: PropTypes.func,
  status: PropTypes.string,
  description: PropTypes.string,
};

LockDesc.defaultProps = {
  onClick: () => {},
  status: '',
  description: '',
};



