import React, { Component } from 'react';
import { InputItem, Modal, Flex, Button, List, Toast } from 'antd-mobile';
import { createForm } from 'rc-form';
import PropTypes from 'prop-types';
/* eslint-disable */
import { ERROR_MSG, CONST_CODE } from '../../../constant';
import { iToast, isMobile } from '../../../util';

import { sendCode, lockAccount } from '../../../action';

const reTry = false;
/* eslint-enable */

let retryTimer = null;

class LockMobile extends Component {
  state = {
    modal: false,
    modalContent: '',
    showModal: false,
    retryTime: 0,
  };

  onClickCheck = (e) => {
    e.preventDefault();
    /* eslint-disable */
    this.props.form.validateFields((err, values) => {
      /* eslint-enable */
      if (!values.mobile) {
        iToast.show(ERROR_MSG.MOBILE_EMPTY);
        return;
      }
      const mobile = values.mobile.replace(/\s/g, '');
      if (!isMobile(mobile)) {
        iToast.show(ERROR_MSG.MOBILE_ERROE);
        return;
      }
      const mobileCode = values.mobileCode;
      if (!mobileCode) {
        iToast.show(ERROR_MSG.CODE_EMPTY);
        return;
      }
      if (!err) {
        const { appInfo } = this.props;
        Toast.loading('...', 100, () => {}, true);
        lockAccount({
          mobile,
          code: mobileCode,
          app_id: CONST_CODE.DOUYIN_AID,
          device_id: appInfo.device_id || '',
        })
          .then((res) => {
            Toast.hide();
            this.props.onClick('success', res.description);
          })
          .catch((res) => {
            Toast.hide();
            if (res) {
              this.props.onClick('fail', res.description);
            }
          });
      }
    });
  };

  fetchCode = () => {
    /* eslint-disable */
    this.props.form.validateFields((err, values) => {
      /* eslint-enable */
      let mobile = values.mobile || '';
      mobile = mobile.replace(/\s/g, '');
      if (!mobile) {
        iToast.show(ERROR_MSG.MOBILE_EMPTY);
        return;
      }
      if (!isMobile(mobile)) {
        iToast.show(ERROR_MSG.MOBILE_ERROE);
        return;
      }
      sendCode({
        mobile,
        type: CONST_CODE.LOCK_CODE,
        aid: CONST_CODE.DOUYIN_AID,
      })
        .then((res) => {
          if (res && res.retry_time) {
            this.setState(
              {
                retryTime: res.retry_time,
              },
              this.countdown,
            );
          }
        })
        .catch((res) => {
          if (res.description) {
            iToast.show(res.description);
          }
        });
    });
  };

  countdown = () => {
    retryTimer = setInterval(() => {
      const { retryTime } = this.state;
      if (retryTime > 0) {
        this.setState({ retryTime: retryTime - 1 });
      } else {
        clearInterval(retryTimer);
      }
    }, 1000);
  };

  onClose = () => {
    this.setState({
      modal: false,
    });
  };

  componentWillUnmount() {
    clearInterval(retryTimer);
  }

  render() {
    /* eslint-disable */
    const { form } = this.props;
    let extraView = (
      <a onClick={this.fetchCode} style={{ color: 'rgba(255, 255, 255, 0.9)' }}>
        
      </a>
    );
    /* eslint-enable */
    const { getFieldProps } = form;
    const { retryTime, modal, modalContent } = this.state;
    if (retryTime > 0) {
      extraView = <a style={{ width: '90px' }}>{`${retryTime}s`}</a>;
    } else {
      clearInterval(retryTimer);
    }
    return (
      <div className="all top-gap">
        <div className="i-body">
          <div className="body-content">
            <List className="i-input">
              <InputItem
                className="i-input-item"
                {...getFieldProps('mobile')}
                clear
                placeholder="11"
                type="phone"
                maxLength={13}
              >
                
              </InputItem>
              <InputItem
                {...getFieldProps('mobileCode')}
                clear
                extra={extraView}
                placeholder=""
                maxLength={4}
                className="i-input-item"
              >
                
              </InputItem>
            </List>
            <div className="my-check">
              <Flex>
                <Flex.Item>
                  <Button
                    type="warning"
                    className="check-button"
                    onClick={this.onClickCheck}
                  >
                    
                  </Button>
                </Flex.Item>
              </Flex>
            </div>
          </div>
        </div>
        <div>
          <Modal
            visible={modal}
            transparent
            maskClosable={false}
            onClose={this.onClose}
            footer={[
              {
                text: '',
                onPress: () => {
                  this.onClose();
                },
              },
            ]}
            wrapProps={{ onTouchStart: this.onWrapTouchStart }}
          >
            <div className="content-notitle">{modalContent}</div>
          </Modal>
        </div>
      </div>
    );
  }
}

const LockMobileForm = createForm()(LockMobile);

export default LockMobileForm;

LockMobile.propTypes = {
  onClick: PropTypes.func,
  appInfo: PropTypes.object,
};

LockMobile.defaultProps = {
  onClick: () => {},
  appInfo: {},
};



